#myTestimonial{
  margin: 30px 0px;
}
#myTestimonial .carousel-inner{
  text-align: center;
  max-width: 70%;
  margin: 0 auto;
}
#myTestimonial  .carousel-inner img{
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: block;
  margin: 0 auto;
}
#myTestimonial .carousel-indicators{
  position: relative;
}
#myTestimonial .fa-angle-left:before,
#myTestimonial .fa-angle-right:before
{
  color:#000;
}
#myTestimonial .carousel-indicators li{
  background-color: #000;
}

/* equal card height */
#postsCarousel .row-equal > div[class*='col-'] {
  display: flex;
  flex: 1 0 auto;
}

#postsCarousel .row-equal .card {
 width: 100%;
}

/* ensure equal card height inside carousel */
#postsCarousel .carousel-inner>.row-equal.active, 
#postsCarousel .carousel-inner>.row-equal.next, 
#postsCarousel .carousel-inner>.row-equal.prev {
  display: flex;
}

/* prevent flicker during transition */
#postsCarousel .carousel-inner>.row-equal.active.left, 
#postsCarousel .carousel-inner>.row-equal.active.right {
  opacity: 0.5;
  display: flex;
}


/* control image height */
#postsCarousel .card-img-top-250 {
  max-height: 250px;
  overflow:hidden;
}


#postsCarousel .card-header{color:dodgerblue}
.delete-btn {
  float: right;
  color: #e74944;
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

